   ========================================================================== */
.o-table__cell--right-align {
  text-align: right;
}

.o-table-wrapper--scrolling {
  box-sizing: border-box;
  overflow-y: hidden;
}
.o-table-wrapper--scrolling table {
  border: 1px solid var(--gray-40);
}
